import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class Registration extends Component {
    render() {
        return (
  <div> 
  <div className="container h-100">
    <div className="row justify-content-center h-100 align-items-center">
      <div className="col-xl-5 col-md-6">
      <div className="mini-logo text-center my-5">
      <Link  to="/"> <img src="assets/images/blogo.jpg" 
      alt="" style={{width:"200px"}}/></Link>
      </div>
        <div className="auth-form card">
          <div className="card-header justify-content-center">
            <h4 className="card-title">Sign up your account</h4>
          </div>
          <div className="card-body">
            <form name="myform" className="signup_validate">
            <div className="container">
              <div className="row justify-content-between align-items-center">
            <div className="col-xl-6 col-lg-6 col-12">
              <div className="form-group">
                <label>First Name</label>
                <input type="text" className="form-control" name="firstname" required/>
              </div>
              </div>
              <div className="col-xl-6 col-lg-6 col-12">
              <div className="form-group">
                <label>Last Name</label>
                <input type="text" className="form-control" name="lastname" required/>
              </div>
              </div>
              </div>
              
              <div className="form-group">
                <label>Phone Number</label>
                <input type="text" className="form-control" placeholder="" name="phone" required />
              </div>
              <div className="form-group">
                <label>Email</label>
                <input type="email" className="form-control" name="email" />
              </div>
              <div className="form-group">
                <label>Password</label>
                <input type="password" className="form-control" placeholder="" name="password" />
              </div>
              <div className="text-center mt-4">
                <button type="submit" className="btn btn-success btn-block">Sign up</button>
              </div>
              </div>
            </form>
            <div className="new-account mt-3">
              <p>Already have an account? <Link className="text-primary" to="/login">Sign in</Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>




            </div>
        )
    }
}

export default Registration
