import React from 'react';
import { HashRouter, Switch } from 'react-router-dom';
import Home from './pages/Home';
import Login from './pages/Login';
import Registration from './pages/Registration';
import Help from './pages/Help';
import About from './pages/About';
import Price from './pages/Price';
import Privacy from './pages/Privacy';
import Otp1 from './LoginVerify/Otp1';
import Otp2 from './LoginVerify/Otp2';
import Reset from './LoginVerify/Reset';
import Lock from './LoginVerify/Lock';
import AppRoute from './RouteConfg/AppRoute';
import MainLayout from './Layout/MainLayout';
import DashboardLayout from './Layout/DashboardLayout';
import Dashboard from './Admin/Users/Dashboard';
import BuySell from './Admin/Users/BuySell';
import Settings from './Admin/Users/Settings';
import AccountSetting from './Admin/Users/AccountSetting';
import Security from './Admin/Users/Security';
import AddDebitCard from './Admin/Users/AddDebitCard';
import AddBank from './Admin/Users/AddBank';
import Verify1 from './Admin/Users/Verify1';
import Verify2 from './Admin/Users/Verify2';
import Verify3 from './Admin/Users/Verify3';
import Verify4 from './Admin/Users/Verify4';
import Verify5 from './Admin/Users/Verify5';
import Verify6 from './Admin/Users/Verify6';
import Historys from './Admin/Users/Historys';
import Loginlayout from './Layout/Loginlayout';
import Contact from './pages/Contact';
import AdminPage from './Admin/AdminPage';
import AdminLayout from './Layout/Admin/AdminLayout';
import AdminHis from './Admin/AdminHis';
import UsersList from './Admin/UsersList';



function App() {
  return (
    <HashRouter basename={process.env.PUBLIC_URL}>
    <Switch>
        <AppRoute exact path="/" component={Home} layout={MainLayout} />
        <AppRoute path="/price" component={Price} layout={MainLayout} />
        <AppRoute path="/about" component={About} layout={MainLayout} />
        <AppRoute path="/help" component={Help} layout={MainLayout}/>
        <AppRoute path="/policy" component={Privacy} layout={MainLayout}/>
        <AppRoute path="/contact" component={Contact} layout={MainLayout}/>
        <AppRoute path="/otp1" component={Otp1} layout={Loginlayout}/>
        <AppRoute path="/otp2" component={Otp2} layout={Loginlayout}/>
        <AppRoute path="/reset" component={Reset} layout={Loginlayout}/>
        <AppRoute path="/login" component={Login} layout={Loginlayout}/>
        <AppRoute path="/reg" component={Registration} layout={Loginlayout}/>
        <AppRoute path="/lock" component={Lock} layout={Loginlayout}/>
        {/* Dashboard Links */}
        <AppRoute path="/users" component={Dashboard} layout={DashboardLayout}/>
        <AppRoute path="/buySell" component={BuySell} layout={DashboardLayout}/>
        <AppRoute path="/settings" component={Settings} layout={DashboardLayout}/>
        <AppRoute path="/accountSetting" component={AccountSetting} layout={DashboardLayout}/>
        <AppRoute path="/security" component={Security} layout={DashboardLayout}/>
        <AppRoute path="/addDebit" component={AddDebitCard} layout={DashboardLayout}/>
        <AppRoute path="/addBank" component={AddBank} layout={DashboardLayout}/>
        <AppRoute path="/historys" component={Historys} layout={DashboardLayout}/>
        <AppRoute path="/verify1" component={Verify1} layout={DashboardLayout} />
        <AppRoute path="/verify2" component={Verify2} layout={DashboardLayout} />
        <AppRoute path="/verify3" component={Verify3} layout={DashboardLayout} />
        <AppRoute path="/verify4" component={Verify4} layout={DashboardLayout} />
        <AppRoute path="/verify5" component={Verify5} layout={DashboardLayout} />
        <AppRoute path="/verify6" component={Verify6} layout={DashboardLayout} />
        <AppRoute path="/admin" component={AdminPage} layout={AdminLayout} />
        <AppRoute path="/adminHistory" component={AdminHis} layout={AdminLayout} />
        <AppRoute path="/userList" component={UsersList} layout={AdminLayout} />
    </Switch>
    </HashRouter>
    
    
    
  );
}

export default App;
