import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export class Login extends Component {
   constructor(props){
       super(props);
       this.onSubmitHandler =this.onSubmitHandler.bind(this);
   }
    onSubmitHandler =(e)=> {
        e.preventDefault();
        this.props.history.push('/otp1');
      
    }

    render() {
        return (
  <div>
    
  <div className="container h-100">
    <div className="row justify-content-center h-100 align-items-center">
      <div className="col-xl-5 col-md-6">
      <div className="mini-logo text-center my-5">
      <Link  to="/"> <img src="assets/images/blogo.jpg" 
      alt="" style={{width:"200px"}}/></Link>
     
      
      {/* <Link className="page-back text-muted" to="/">
            <span><i className="fa fa-angle-left" /></span>Back Home</Link> */}
      </div>
      
     
        <div className="auth-form card">
          <div className="card-header justify-content-center">
            <h4 className="card-title">Sign in</h4>
          </div>
          
          <div className="card-body">
           <form onSubmit={this.onSubmitHandler} className="signin_validate">

              <div className="form-group">
                <label>Email</label>
                <input type="email" className="form-control" placeholder="hello@example.com" name="email" required />
              </div>
              <div className="form-group">
                <label>Password</label>
                <input type="password" className="form-control" placeholder="Password" name="password" required />
              </div>
              

              
                
              <div className="form-row d-flex justify-content-between mt-4 mb-2">
                <div className="form-group mb-0">
                  <label className="toggle">
                    <input className="toggle-checkbox" type="checkbox" />
                    <span className="toggle-switch" />
                    <span className="toggle-label">Remember me</span>
                  </label>
                </div>
                <div className="form-group mb-0">
                  <Link to="/reset">Forgot Password?</Link>
                </div>
              </div> 
              
              <div className="text-center">
                <button type="submit" className="btn btn-success btn-block">Sign in</button>
              </div>
              <div className="cen">
              <p>Or</p>
              <hr/>
                      <p>Sign With</p>
                   
                 <div className="google-btn">
                   <div className="google-icon-wrapper">
 
            <div className="google-icon" ><i className="fa fa-google" style={{color: "red",fontSize:"25px"}} aria-hidden="true"></i></div>
             </div>
              <p className="btn-text "><b>Sign in with google</b></p>
                </div>
                </div>
          </form>
            <div className="new-account mt-3">
              <p>Don't have an account? <Link to="/reg" className="text-primary">Sign
                up</Link></p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>

        )
    }
}

export default Login
