import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class Settings extends Component {
    render() {
        return (
           
  <div className="content-body">
    <div className="container-fluid">
      <div className="row">
        <div className="col-xl-3 col-md-4">
          <div className="card settings_menu">
            <div className="card-header">
              <h4 className="card-title">Settings</h4>
            </div>
            <div className="card-body">
              <ul>
                <li className="nav-item">
                  <Link to="/settings" className="nav-link active">
                    <i className="la la-user" />
                    <span>Edit Profile</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/security" className="nav-link">
                    <i className="la la-lock" />
                    <span>Security</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/accountSetting" className="nav-link">
                    <i className="la la-university" />
                    <span>Linked Account</span>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-xl-9 col-md-8">
          <div className="row">
            
            <div className="col-xl-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="card-title">Personal Information</h4>
                </div>
                <div className="card-body">
                  <form method="post" name="myform" className="personal_validate">
                    <div className="form-row">
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Your Name</label>
                        <input type="text" className="form-control" placeholder="Maria Pascle" name="fullname" />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Email</label>
                        <input type="email" className="form-control" placeholder="Hello@example.com" name="email" />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Date of birth</label>
                        <input type="text" className="form-control" placeholder="10-10-2019" id="datepicker" autoComplete="off" name="dob" />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Bvn</label>
                        <input type="text" className="form-control" placeholder="563456789" name="bvn" required />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Permanent Address</label>
                        <input type="text" className="form-control" placeholder="123, Central Square, Brooklyn" name="permanentaddress" />
                      </div>
                      
                    </div>
                    <div className="row">
                      <div className="form-group col-2">
                        <button className="btn btn-success pl-5 pr-5">Edit</button>
                      </div>
                      <div className="form-group col-10">
                        <button className="btn btn-success pl-5 pr-5">Save</button>
                      </div>
                      </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
  
    }
        }
  
export default Settings;
